export interface IPost {
  id: String;
  title: String;
  content: String;
  contentType: ContentType;
  dateTime: String;
  catagories: ICatagories;
  source: String;
}

export enum ContentType {
  Video,
  Image,
}

export type ICatagories =
  | "PoliceActivity"
  | "SocialActivity"
  | "Award"
  | "Reward"
  | "Achivment"
  | "MediaCoverage"
  | "MediaStories"
  | "SopnoPuron";

export const initPost: IPost = {
  id: new Date().getUTCMilliseconds().toString(),
  title: "no title",
  content: "",
  contentType: ContentType.Image,
  source: "",
  catagories: "PoliceActivity",
  dateTime: new Date().toISOString(),
};
