const PostCase: FC = () => {
  const [posts, setposts] = useState<IPost[]>([]);
  const [load, setLoad] = useState(false);
  const dispatch = useAppDispatch();
  const [error, seterr] = useState<String | null>(null);
  useEffect(() => {
    setLoad(true);
    get(child(ref(database), `posts/`))
      .then((snapshot) => {
        const result: IPost[] = [];
        snapshot.forEach((e) => {
          e.forEach((e) => {
            result.push(e.val());
            console.log(e.val());
          });
        });
        setposts(result);
        dispatch(addPosts(result));
        setLoad(false);
      })
      .catch((e) => {
        setLoad(false);
        console.log(e);

        seterr(` Somehing went wrong ${e}`);
      });
  }, [dispatch]);

  return (
    <div className="blog_main">
      <div className="container">
        <div className="row">
          <div className="col-md-12">
            <div className="titlepage">
              <h2>Our Blogs</h2>
              <span>
                It is a long established fact that a reader will be distracted
                by the readable content of a page{" "}
              </span>
            </div>
          </div>
        </div>
        {load ? (
          <h2>LOADING.....</h2>
        ) : (
          <>
            <div> {error && error}</div>

            <PostList posts={posts} />
          </>
        )}
      </div>
    </div>
  );
};

export default PostCase;

