import { FC } from "react";
import { IPost } from "../../interface/post.interface";
import PostDetails from "./PostDetails";
const PostList: FC<{ posts: IPost[] }> = ({ posts }) => {
  return (
    <div>
      {posts.map((post) => {
        return <PostDetails key={post.id.toString()} {...post} />;
      })}
    </div>
  );
};

export default PostList;
