import { FormEvent, useState } from "react";
import { Button, Form, ProgressBar, Spinner } from "react-bootstrap";
import { database, storage } from "../../firebase";
import {
  ref,
  uploadBytesResumable,
  getDownloadURL,
  StorageError,
} from "firebase/storage";
import {
  IPost,
  ICatagories,
  ContentType,
} from "../../interface/post.interface";
import { child, push, ref as dbref, update } from "firebase/database";
import { useNavigate } from "react-router-dom";

const AddPost = () => {
  const [file, setfile] = useState<File | null>(null);
  const [progress, setprogress] = useState(0);
  const [imageUrl, setImageUrl] = useState<String | null>(null);
  const [videosrc, setvideosrc] = useState<String | null>(null);
  const [isImage, setisImage] = useState<boolean>(false);
  const [uploadError, setuploadError] = useState<StorageError | null>(null);
  const [title, settitle] = useState("");
  const [content, setcontent] = useState("");
  const [loading, setloading] = useState(false);
  const [catagory, setcatagory] = useState<ICatagories>("PoliceActivity");

  const navigate = useNavigate();

  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
    setloading(true);

    if (title && content && catagory) {
      const newPostKey = push(child(dbref(database), "posts/" + catagory)).key;
      const post: IPost = {
        title,
        content,
        catagories: catagory,
        contentType: imageUrl ? ContentType.Image : ContentType.Video,
        dateTime: new Date().toISOString(),
        id: newPostKey ?? new Date().getUTCMilliseconds().toString(),
        source: imageUrl ?? videosrc!,
      };

      update(dbref(database, `posts/${post.catagories}/${post.id}`), post)
        .then((res) => {
          console.log(res);

          setloading(false);
          navigate("/");
        })
        .catch((e) => {
          console.log(e);

          setloading(false);
        });
    }
  };

  const uploadFile = (imageFile: File) => {
    const storageRef = ref(storage, "images/" + imageFile.name);
    const uploadTask = uploadBytesResumable(storageRef, imageFile, {
      contentType: "image/jpeg",
    });
    uploadTask.on(
      "state_changed",
      (snapshot) => {
        // Observe state change events such as progress, pause, and resume
        // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
        const progress =
          (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        setprogress(progress);
        console.log("Upload is " + progress + "% done");
        switch (snapshot.state) {
          case "paused":
            console.log("Upload is paused");
            break;
          case "running":
            console.log("Upload is running");
            break;
        }
      },
      (error) => {
        setuploadError(error);
      },
      () => {
        // Handle successful uploads on complete
        // For instance, get the download URL: https://firebasestorage.googleapis.com/...
        getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
          console.log(downloadURL);

          setImageUrl(downloadURL);
          console.log(imageUrl);
        });
      }
    );
  };
  return (
    <div className="container mt-5">
      <div className="row">
        <div className="col-md-7 mx-auto">
          <Form onSubmit={handleSubmit}>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>Title</Form.Label>
              <Form.Control
                type="text"
                placeholder="Your Title"
                onChange={(e) => settitle(e.target.value)}
              />
            </Form.Group>
            <Form.Group
              className="mb-3"
              controlId="exampleForm.ControlTextarea1"
            >
              <Form.Label>Content of Post</Form.Label>
              <Form.Control
                as="textarea"
                rows={6}
                onChange={(e) => setcontent(e.target.value)}
              />
            </Form.Group>
            <Form.Group className="mt-3">
              <Form.Label> Catagories</Form.Label>
              <Form.Select
                value={catagory}
                onChange={(e) => setcatagory(e.target.value as ICatagories)}
              >
                <option value="PoliceActivity">Police Activity</option>
                <option value="SocialActivity">Social Activity</option>
                <option value="Achivment">Achivment</option>
                <option value="Reward">Reward</option>
                <option value="Award">Award</option>
                <option value="MediaCoverage">Media Coverage</option>
                <option value="MediaStories">Media Special Story</option>
                <option value="SopnoPuron">Sopnopuron</option>
              </Form.Select>
            </Form.Group>

            <Form.Group className="mt-4">
              <Form.Check
                inline
                label="Image"
                name="group1"
                type="checkbox"
                onChange={(_) => {
                  setisImage(!isImage);

                  console.log(isImage);
                }}
                id="inline-radio-2"
              />
            </Form.Group>
            {isImage ? (
              <>
                <Form.Group controlId="formFile" className="mb-3">
                  <Form.Label>Post Image</Form.Label>
                  <div className="row">
                    <div className="col-md-8">
                      <div className="form-control">
                        <input
                          type="file"
                          multiple
                          onChange={(e) => {
                            setfile(e.target.files?.item(0) ?? null);
                            console.log(file);
                          }}
                        />
                      </div>
                    </div>
                    <div className="col-md-4">
                      <Button
                        disabled={file === null}
                        className="btn btn-danger"
                        onClick={() => uploadFile(file!)}
                      >
                        Upload Image
                      </Button>
                      <div>{uploadError && uploadError.message}</div>
                    </div>
                  </div>
                </Form.Group>
                <>
                  <p>Uploading Images</p>
                  <div className="row">
                    <div className="col-md-8">
                      <ProgressBar animated now={progress} />
                    </div>
                    <div className="col-md-4">
                      <div className="btn btn-sm btn-danger">Pause</div>
                      <div className="btn btn-sm btn-success ">Resume</div>
                    </div>
                    {}
                  </div>
                </>
              </>
            ) : (
              <>
                <Form.Group>
                  <Form.Label>Video Source</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Youtube Video Link"
                    onChange={(e) => setvideosrc(e.target.value)}
                  />
                </Form.Group>
              </>
            )}
            <Form.Group>
              <Button
                variant="success"
                type="submit"
                className="w-100 mt-3"
                disabled={!(imageUrl || videosrc)}
              >
                Post
              </Button>

              {loading ? (
                <div>
                  <Spinner animation="border" role="status">
                    <span>Loading...</span>
                  </Spinner>
                </div>
              ) : (
                <></>
              )}
            </Form.Group>
          </Form>
        </div>
      </div>
    </div>
  );
};

export default AddPost;
