import { FC, useEffect } from "react";
import { BrowserRouter, Route, Routes, useNavigate } from "react-router-dom";
import Login from "./features/auth/Login";
import Register from "./features/auth/Register";
import HomePage from "./pages/HomePage";

import { onAuthStateChanged } from "firebase/auth";
import { authFire } from "./firebase";
import { useAppDispatch } from "./app/hooks";
import { initUser } from "./features/auth/authSlice";
import AddPost from "./component/AddPost/AddPost";
import CatagoriesPage from "./pages/Catagories";

function App() {
  const dispatch = useAppDispatch();

  useEffect(() => {
    onAuthStateChanged(authFire, (currentUser) => {
      if (currentUser) {
        dispatch(initUser(currentUser));
      }
    });
  }, [dispatch]);

  return (
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<HomePage />} />
        <Route path="/catagory/:cat" element={<CatagoriesPage />} />
        <Route path="/about" element={<About />} />
        <Route path="/login" element={<Login />} />
        <Route path="/register" element={<Register />} />
        <Route path="/addPost" element={<AddPost />} />
      </Routes>
    </BrowserRouter>
  );
}

export default App;

const About: FC = () => {
  const navigate = useNavigate();
  return (
    <>
      <h2>This is About Page</h2>
      <button className="btn btn-danger" onClick={() => navigate(-1)}>
        Go back
      </button>
    </>
  );
};
