import { child, get, ref } from "firebase/database";
import { FC, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import PostList from "../component/PostCase/PostList";
import { database } from "../firebase";
import { IPost } from "../interface/post.interface";

const CatagoriesPage: FC = () => {
  const { cat } = useParams();
  const [posts, setposts] = useState<IPost[]>([]);
  const [errror, seterrror] = useState<String | null>(null);
  const [load, setLoad] = useState(false);
  useEffect(() => {
    setLoad(true);
    get(child(ref(database), `posts/${cat}`))
      .then((snapshot) => {
        const results: IPost[] = [];
        if (snapshot.exists()) {
          snapshot.forEach((e) => {
            results.push(e.val());
          });
        }
        setposts(results);
        setLoad(false);
      })
      .catch((e) => {
        seterrror(`${e}`);
        setLoad(false);
        console.log(e);
      });
  }, [cat]);

  return (
    <>
      <h3>Catagoris Page</h3>
      <div>{errror && errror}</div>

      {load ? <h2>"Loading...."</h2> : <PostList posts={posts} />}
    </>
  );
};

export default CatagoriesPage;
